import React, { Component } from 'react'
import TopBar from '../components/TopBar';
import Explore from './Explore';
import Search from './Search';
import { BrowserRouter, Route } from 'react-router-dom'
import ItemDetail from '../components/ItemDetail';

export default class Photos extends Component {
    constructor(props) {
        super(props);
        this.state = {
            photo: [],
            page: 1,
            perPage: 20,
            maxPage: null,
            col: 3
        }
        this.onChange = this.onChange.bind(this);
    }
    onChange(name) {
        return (event) => {
            event.preventDefault();
            this.setState({ [name]: Number(event.target.value) });
        }
    }
    render() {
        return (
            <div>

                <BrowserRouter>
                    <div>
                        <Route path="*" component={(props) => <TopBar onChange={this.onChange} col={this.state.col} perPage={this.state.perPage} {...props}/>}/>
                        <Route path="/Photos/" exact component={(props) => <Explore col={this.state.col} perPage={this.state.perPage} {...props} />} />
                        <Route exact path="/Photos/item/:id" component={ItemDetail} />
                        <Route path="/Photos/search*" component={(props) => <Search col={this.state.col} perPage={this.state.perPage} {...props}/>} />
                    </div>
                </BrowserRouter>

            </div>
        )
    }
}



