import React, { Component } from 'react'
import SearchBar from '../components/SearchBar';
import List from '../components/List';
import handleScroll from '../modules/handleScroll';


export default class Search extends Component {
    constructor(props) {
        super(props);
        this.state = {
            searchText: this.props.match.params[0].substr(1),
            photo: [],
            page: null,
            maxPage: null,
            perPage: 20
        }
        this.handleChange = this.handleChange.bind(this)
        this.search = this.search.bind(this);
        this.loadMore = this.loadMore.bind(this);
        this.reset = this.reset.bind(this);
        this.handleScroll = handleScroll(this.loadMore);
    }

    handleChange(e) {
        this.setState({
            searchText: e.target.value
        })
    }

    componentDidMount(){
        window.addEventListener('scroll', this.handleScroll);
        if(this.state.searchText!== "")
            this.search();
    }
    componentWillUnmount(){
        window.removeEventListener('scroll', this.handleScroll);
        console.log('unmount')
    }

    async loadMore() {
        let req, res, data;
        const { page, searchText, maxPage, perPage } = this.state;
        console.log(page)
        if ((page + 1) > maxPage) return;
        try {
            req = await fetch(`https://api.flickr.com/services/rest/?method=flickr.photos.search&api_key=ac9fd35025c930e3ea8d613813e10434&tags=${searchText}&extras=owner_name%2Curl_n%2Cviews&per_page=${perPage}&page=${(page + 1)}&format=json&nojsoncallback=1`)
            res = await req.json();
            data = res.photos;
            this.setState({
                photo: this.state.photo.concat(data.photo),
                page: data.page,
                maxPage: data.pages
            })
        } catch (error) {

        }
    }
    async search() {
        let req, res, data;
        const { searchText, perPage } = this.state;
        try {
            req = await fetch(`https://api.flickr.com/services/rest/?method=flickr.photos.search&api_key=ac9fd35025c930e3ea8d613813e10434&tags=${searchText}&extras=owner_name%2Curl_n%2Cviews&per_page=${perPage}&page=1&format=json&nojsoncallback=1`)
            res = await req.json();
            data = res.photos;
            this.setState({
                photo: data.photo,
                page: data.page,
                maxPage: data.pages
            })
        } catch (error) {

        }
    }

    async reset(){
        let req, res, data;
        const { searchText, perPage } = this.state;
        try {
            req = await fetch(`https://api.flickr.com/services/rest/?method=flickr.photos.search&api_key=ac9fd35025c930e3ea8d613813e10434&tags=${searchText}&extras=owner_name%2Curl_n%2Cviews&per_page=${perPage}&page=1&format=json&nojsoncallback=1`)
            res = await req.json();
            data = res.photos;
            this.setState({
                photo: data.photo,
                page: data.page,
                maxPage: data.pages
            })
        } catch (error) {

        }
    }

    render() {
        return (
            <div style={{ flex: 1, marginTop: 70 }}>
                <SearchBar onChange={this.handleChange} search={this.search} value={this.state.searchText}/>
                <List tileData={this.state.photo} col={this.props.col} history={this.props.history}/>
            </div>
        )
    }
}



