import React, { Component } from 'react'
import List from '../components/List'
import handleScroll from '../modules/handleScroll';

export default class Explore extends Component {
    constructor(props) {
        super(props);
        this.state = {
            photo: [],
            page: 1,
            maxPage: null,
            perPage: this.props.perPage
        }
        this.reset = this.reset.bind(this);
        this.loadMore = this.loadMore.bind(this);
        this.handleScroll = handleScroll(this.loadMore);
    }

    async componentDidMount(){
        window.addEventListener('scroll', this.handleScroll);
        let req, res, data;
        const { perPage } = this.state;
        try {
            req = await fetch(`https://api.flickr.com/services/rest/?method=flickr.interestingness.getList&api_key=ac9fd35025c930e3ea8d613813e10434&extras=owner_name%2Curl_n%2Cviews&per_page=${perPage}&page=1&format=json&nojsoncallback=1`);
            res = await req.json();
            data = res.photos;
            this.setState({
                photo: data.photo,
                page: data.page,
                maxPage: data.pages
            })
        } catch (error) {
            console.log(error)
        }
    }

    componentWillUnmount(){
        window.removeEventListener('scroll', this.handleScroll);
    }

    async reset(){
        let req, res, data;
        const { perPage } = this.state;
        try {
            req = await fetch(`https://api.flickr.com/services/rest/?method=flickr.interestingness.getList&api_key=ac9fd35025c930e3ea8d613813e10434&extras=owner_name%2Curl_n%2Cviews&per_page=${perPage}&page=1&format=json&nojsoncallback=1`);
            res = await req.json();
            data = res.photos;
            this.setState({
                photo: data.photo,
                page: data.page,
                maxPage: data.pages
            })
        } catch (error) {
            console.log(error)
        }
    }

    async loadMore(){
        let req, res, data;
        const { page, perPage, maxPage } = this.state;
        if(page + 1>maxPage) return;
        try {
            req = await fetch(`https://api.flickr.com/services/rest/?method=flickr.interestingness.getList&api_key=ac9fd35025c930e3ea8d613813e10434&extras=owner_name%2Curl_n%2Cviews&per_page=${perPage}&page=${page + 1}&format=json&nojsoncallback=1`);
            res = await req.json();
            data = res.photos;
            this.setState({
                photo: this.state.photo.concat(data.photo),
                page: data.page,
                maxPage: data.pages
            })
        } catch (error) {
            console.log(error)
        }
    }

    render() {
        let {col, history} = this.props;
        return (
            <div style={{ flex: 1, marginTop: 70 }}>
                <List tileData={this.state.photo} col={col} history={history}/>
            </div>
        )
    }
}




