import React from 'react'
import {Input} from '@material-ui/core'

export default ({onChange, search, value}) => {
    return (
        <div style={{marginTop: 70, borderRadius: 10, backgroundColor: '#d3d3d3',display: 'flex', marginBottom: 10, alignItems: 'center' }}>
            <Input
                placeholder="Search…"
                disableUnderline
                style={{paddingLeft: 10}}
                onChange={onChange}
                onKeyUp={(key) => key.keyCode === 13 ? search() : null}
                value={value}
            />
        </div>
    )
}


