import React, { Component } from 'react';
import image from '../images/30.gif'
class ItemDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            info: null,
            urls: null,
            likes: null,
            isLoading: true
        }
    }
    async componentDidMount() {
        let reqInfo, reqUrl, reqLikes, resLikes, resInfo, resUrl, info, urls, likes;
        try {
            reqInfo = await fetch(`https://api.flickr.com/services/rest/?method=flickr.photos.getInfo&api_key=ac9fd35025c930e3ea8d613813e10434&photo_id=${this.props.match.params.id}&format=json&nojsoncallback=1`)
            reqUrl = await fetch(`https://api.flickr.com/services/rest/?method=flickr.photos.getSizes&api_key=ac9fd35025c930e3ea8d613813e10434&photo_id=${this.props.match.params.id}&format=json&nojsoncallback=1`)
            reqLikes = await fetch(`https://api.flickr.com/services/rest/?method=flickr.photos.getFavorites&api_key=ac9fd35025c930e3ea8d613813e10434&photo_id=${this.props.match.params.id}&format=json&nojsoncallback=1`)
            resInfo = await reqInfo.json();
            resUrl = await reqUrl.json();
            resLikes = await reqLikes.json();
            info = resInfo.photo;
            urls = resUrl.sizes;
            likes = resLikes.photo.total;
            this.setState({
                info: info,
                urls: urls,
                likes: likes,
                isLoading: false
            })
        } catch (error) {
            console.error(error)
        }
    }
    render() {
        console.log(this.state.info)
        if (this.state.isLoading)
            return (
                <div style={{ marginTop: 70, height: '100%', alignItems: 'center', justifyContent: 'center', display: 'flex' }}>
                    <img src={image} alt="loading" />
                </div>
            )
        const { info } = this.state;
        return (
            <div style={{ marginTop: 70, alignItems: 'center', justifyContent: 'center', display: 'flex', flexDirection: 'column' }}>
                <div style={{ width: '100%', height: '500px', backgroundColor: '#212124' }}>
                    <div style={{ width: '100%', height: '500px', backgroundImage: `url(${this.state.urls.size[6].source})`, backgroundSize: 'auto', backgroundPosition: 'center', backgroundRepeat: 'no-repeat' }}>
                    </div>
                </div>
                <div style={{ display: 'grid', gridTemplateColumns: '500px 500px', marginTop: 20 }}>
                    <div style={{ display: 'flex', flexDirection: 'column', paddingRight: 50 }}>
                        <div style={{ float: 'left' }}>
                            <div style={{ color: '#212124', top: '17px', fontSize: '20px', fontWeight: '600', marginBottom: 15 }}>{info.owner.realname !== "" ? info.owner.realname : info.owner.username}</div>
                            <div style={{ color: '#212124', top: '17px', fontSize: '16px', fontWeight: '600', lineHeight: '18px', marginBottom: 10 }}>{info.title._content}</div>
                            <div style={{ fontSize: '14px', lineHeight: '18px', fontWeight: 400 }}>{info.description._content}</div>
                        </div>
                    </div>
                    <div style={{ display: 'flex', flexDirection: 'column' }}>
                        <div style={{ display: 'flex', flexDirection: 'row' }}>
                            <div style={{ display: 'flex', flex: 1, flexDirection: 'row', alignItems: 'space-around' }}>
                                <div style={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: "center" }}>
                                    <div>{info.views}</div>
                                    <div>lượt xem</div>
                                </div>
                                <div style={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                    <div>{this.state.likes}</div>
                                    <div>lượt thích</div>
                                </div>
                            </div>
                            <div style={{ flex: 2, textAlign: 'right' }}>Ngày đăng: {info.dates.taken}</div>
                        </div>
                        <div style={{marginTop: 10}}>
                            Tags:
                        <div style={{ display: 'grid', gridTemplateColumns: '125px 125px 125px 125px', marginTop: 10 }}>
                                {info.tags.tag.map((item, index) => <div style={{ textAlign: 'center' }} onClick={() => this.props.history.push(`/search/${item._content}`)}>{item._content}</div>)}
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        );
    }
}

export default ItemDetail;


